<template>
  <div class="main">
    <modal-register v-bind:msg="message" />
    <div v-if="isLogged" class="logged">
      <div id="chats">
        <h2>Chats</h2>
        <!-- <button v-if="!isOpened" @click="this.load">View...</button> -->
        <table id="chatList">
          <tr
            v-for="room in orderBy(rooms, 'timestamp')"
            :key="room.id"
            v-bind:class="[room.owner.id === me.id ? 'owner' : '']"
          >
            <td>{{ room.name }}</td>
            <td>
              <button
                v-if="selected !== room.id"
                id="enterChat"
                @click="enter(room)"
              >
                Enter
              </button>
            </td>
          </tr>
        </table>
      </div>
      <hr />
      <ChatComponent @exit="exit" v-if="isSelected" :room="current" :me="me" />
      <hr />
      <div id="create">
        <h2>Create</h2>
        <ul type="none">
          <li><input v-model="chatName" placeholder="Your chat name" /></li>
          <li><button @click="create">Create</button></li>
        </ul>
      </div>
    </div>
    <div id="alter" v-else>
      <h1>Seems empty? &#128533;</h1>
      <h2>Login first or create an account to start chatting.</h2>
    </div>
  </div>
</template>

<script>
import ModalRegister from "../components/ModalRegister.vue";

import {
  CREATE_ROOM,
  GET_ALL_CHATS,
  ME,
  ROOM_CREATED,
  ROOM_UPDATED,
  ROOM_DELETED,
  JOIN_ROOM,
  EXIT
} from "./../constants/graphql";

import Vue2Filters from "vue2-filters";

export default {
  name: "Chats",
  beforeRouteLeave(to, from, next) {
    console.log(to, from);
    if (this.current !== "") {
      this.exit();
    }
    next();
  },
  components: {
    ChatComponent: () => import('./../components/ChatComponent'),
    ModalRegister
  },
  props: ["is-logged"],
  data() {
    return {
      rooms: [],
      username: "",
      me: "",
      message: "",
      selected: 0,
      current: "",
      chatName: "",
      chatsOpened: false
    };
  },
  mixins: [Vue2Filters.mixin],
  computed: {
    isSelected() {
      return this.selected !== 0;
    }
  },
  apollo: {
    rooms: {
      query: GET_ALL_CHATS
    },
    me: {
      query: ME
    },
    $subscribe: {
      roomCreated: {
        query: ROOM_CREATED,
        result({ data }) {
          const { roomCreated } = data;
          this.rooms.push(roomCreated);
        }
      },
      roomUpdated: {
        query: ROOM_UPDATED,
        result({ data }) {
          const {roomUpdated }= data;
          for (let room in this.rooms){
            if (parseInt(room.id) === parseInt(roomUpdated.id)){
              room = roomUpdated;
            }
          }
        }
      },
      roomDeleted: {
        query: ROOM_DELETED,
        result({ data }) {
          const { roomDeleted } = data;
          for (let room in this.rooms){
            if (parseInt(room.id) === parseInt(roomDeleted.id)){
              const index = this.rooms.indexOf(room.id);
              this.rooms.splice(index, 1);
            }
          }
        }
      }
    }
  },
  methods: {
    async create() {
      const result = await this.$apollo.mutate({
        mutation: CREATE_ROOM,
        variables: {
          name: this.chatName
        }
      });
      console.log(result);
      this.chatName = "";
    },
    async enter(room) {
      try {
        const result = await this.$apollo.mutate({
          mutation: JOIN_ROOM,
          variables: {
            id: room.id
          }
        });
        this.current = result.data.joinRoom;
        this.selected = room.id;
      } catch (err) {
        this.message = err;
        this.$modal.show("notif");
      }
    },
    async exit() {
      try {
        const result = await this.$apollo.mutate({
          mutation: EXIT
        });
        console.log(result);
        console.log(this.$children);
        this.current = "";
        this.selected = 0;
      } catch (err) {
        this.message = err;
        this.$modal.show("notif");
      }
    }
  }
};
</script>

